* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

hr {
  width: 80vw;
}

.nav-bg-container {
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url("../images/hamburger-and-fries-edit.jpg");
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: auto;
}

/* mobile navbar */

#hamburger-link {
  display: none;
}

.navbar-open {
  width: 100vw;
  opacity: 1;
  height: 55vh;
  visibility: visible;
  background-color: #111;
}

.navbar-closed {
  opacity: 0;
  width: 0;
  height: 0;
  visibility: hidden;
  font-family: "Libre Baskerville", serif;
}

.mobile-navbar {
  display: none;
  flex-direction: column;
  background-color: #111;
  background-color: #121212;
  display: flex;
  flex-direction: column;
  text-align: left;
}

.mobile-navbar a {
  color: #fff;
  text-decoration: none;
  font-family: "Libre Baskerville", serif;
}

#hamburger-link span {
  font-size: 7rem;
  color: #fff;
}

#close-btn {
  position: absolute;
  right: 0;
}

.mobile-subnav {
  float: left;
  overflow: hidden;
}

.mobile-menu-links {
  display: flex;
  flex-direction: column;
  line-height: 2rem;
  align-items: flex-start;
  margin-left: 10vw;
  padding-top: 4vh;
}

.mobile-menu-links a {
  margin-right: 35vw;
}

.mobile-subnav-btn {
  background-color: transparent;
  color: #fff;
  border: none;
  font-size: 1.1rem;
  font-family: "Libre Baskerville", serif;
}

.mobile-subnav-content {
  display: none;
  text-align: left;
  padding-left: 5vw;
}

.mobile-subnav-content hr {
  width: 75vw;
}

.mobile-subnav:hover .mobile-subnav-content {
  display: block;
}

.mobile-subnav-content a {
  display: flex;
  flex-direction: column;
}

.navbar-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.navbar {
  overflow: hidden;
  padding-top: 3vh;
  padding-left: 2vw;
}

.navbar a {
  color: white;
  text-decoration: none;
  font-family: "Libre Baskerville", serif;
  float: left;
  padding: 14px 16px;
}

.navbar a:hover {
  text-decoration: underline;
}

.subnav {
  float: left;
  overflow: hidden;
}

.subnav .subnav-btn {
  font-size: 1.3rem;
  border: none;
  outline: none;
  color: white;
  padding: 0.7rem 0.5rem 1.3rem 0.5rem;
  background-color: inherit;
  font-family: inherit;
  margin: 0;
}

.subnav-content {
  display: none;
  position: absolute;
  left: 0;
  background-color: #99752c;
  width: 100%;
  z-index: 1;
}

.subnav-content a {
  float: left;
  color: white;
  text-decoration: none;
}

.subnav-content a:hover {
  background-color: #111;
  color: #99752c;
}

.subnav:hover .subnav-content {
  display: block;
}

.main-logo {
  color: white;
  text-decoration: none;
  font-size: 3rem;
  font-family: "Niconne", cursive;
  padding-top: 3vh;
  line-height: 3rem;
}

.facebook-btn {
  width: 6vw;
  height: 6vh;
}

.instagram-btn {
  width: 6vw;
  height: 6vh;
}

.social-media-btns {
  padding-top: 3vh;
  padding-right: 2vw;
}

.top-menu-header {
  text-align: center;
  color: #fff;
  margin-top: 6vh;
  font-size: 4rem;
}

.our-ingredients-heading {
  text-align: center;
  margin-top: 20vh;
  font-size: 3.5rem;
  color: #99752c;
}

.ingredient-paragraph {
  padding-left: 3vw;
  padding-right: 1vw;
  line-height: 1.5rem;
}

.steak-and-potato-img {
  border-radius: 50%;
  height: 45vh;
  width: 20vw;
}

.our-ingredients-content {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-right: 3vw;
  padding-bottom: 15vh;
  padding-left: 10vw;
  padding-right: 10vw;
  padding-top: 10vh;
}

.our-philosophy-section {
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url("../images/chair-cutlery-dining.jpg");
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 60vh;
}

.our-philosophy {
  text-align: center;
  font-size: 3.5rem;
  color: #fff;
  padding-top: 10vh;
}

.philosophy-paragraph {
  text-align: center;
  padding: 10vh 15vw 0 15vw;
  color: #fff;
  line-height: 1.5rem;
}

/* footer */

#footer {
  background-color: #111;
  color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 4vh;
  padding-bottom: 8vh;
  line-height: 3rem;
}

.main-footer-links {
  display: flex;
  justify-content: space-around;
  width: 25vw;
}

.main-footer-links a {
  font-size: 1rem;
  color: #fff;
  text-decoration: none;
  font-family: "Libre Baskerville", serif;
}

.footer-subnav a {
  color: #fff;
  text-decoration: none;
  font-family: "Libre Baskerville", serif;
}

.footer-subnav {
  display: flex;
  justify-content: space-around;
  width: 30vw;
}

.footer-phone-number:hover {
  color: #99752c;
}

.bottom-footer {
  background-color: #99752c;
  text-align: center;
  color: #fff;
  line-height: 5vh;
}
